export default {
  name: 'Footer',
  props: {
    promo_language_code: {
      type: String,
      required: true,
    },
    promo_country_code: {
      type: String,
      required: true,
    },
    landing_page_type: {
      type: String,
      required: false,
    }
  },
  data(){
      return{
          desktop: true,
          mobile: false,
      }
  },
  destroyed() {
      window.removeEventListener("resize", this.myEventHandler);
  },
  mounted(){
      this.myEventHandler()
      window.addEventListener("resize", this.myEventHandler);
  },
  methods:{
      myEventHandler(e) {
          /* code for handling resize */
          if (window.innerWidth <= 992){
              this.desktop = false
              this.mobile = true
          }
          else {
              this.desktop = true
              this.mobile = false
          }
      }
  },
  computed: {
    responsibleGamingURL: function () {
      let returnURL = ''
      switch (this.landing_page_type) {
        case 'verajohn':
          returnURL = 'https://www.verajohn.com/about/responsible-gaming';
          break;
        case 'intercasino':
          returnURL = 'https://www.intercasino.com/about/responsible-gaming';
          break;
        default:
          returnURL = '#';
          break;
      }
      return returnURL;
    }
  }
};
